<template>
<div class="line-box-demo demo">
<div class="section">
    <h2 class="demo-title">Scrum 面包屑</h2>
    <div class="box">
        <md>
            用途：用于页面内层级导航
        </md>
    </div>
</div>

<div class="section">
    <h2 class="demo-title">Scrum 面包屑</h2>
    <div class="box">
        <scrum :params="params"
            @link-change="changeLevel"></scrum>
    </div>
    <div class="box">
        {{info}}
    </div>
</div>

</div>
</template>

<script type="text/javascript">
import Scrum from 'smui/Scrum'

const scrumList = [
    {text: '计划', name: 'plan'},
    {text: '单元', name: 'unit'},
    {text: '关键词', name: 'keyword'}
]

export default {
    data() {
        return {
            params: {
                items: scrumList
            },
            info: ''
        }
    },
    components: {
        Scrum
    },
    methods: {
        changeLevel(e) {
            this.info = e.data
        }
    }
}
</script>



