<template>
<div class="tab-demo demo">
    <scroll-bar ref="scrollbar"></scroll-bar>
    <div id="scrollbar" style="height: 50px;width:300px;border:1px solid #eee; background:#efefef;overflow:scroll">
        <div style="height: 30px;width: 3000px;border: 1px solid #aaa;background: #ccc;overflow: scroll;"></div>
    </div>
</div>
</template>

<script type="text/javascript">
import ScrollBar from 'smui/ScrollBar'
export default {
    data() {
        return {
            info: ''
        }
    },
    mounted(){
        this.$refs.scrollbar.init(document.getElementById('scrollbar'))
    },
    components: {
        ScrollBar
    }
}
</script>



