<template>
    <div class="region-component-panel">
        <div class="all-region">
            <div class="area-content" @click="clickArea(allRegion.id)">
                <input type="checkbox" :disabled="allRegion.disabled" v-model="allRegion.selected"
                       :class="{'checkbox-indeterminate':allRegion.selectedChildrenNum > 0 && allRegion.selectedChildrenNum < allRegion.children.length}">
                所有地域
            </div>
        </div>
        <ul>
            <li v-for="value in allRegion.children" class="area-li">
                <div class="area-content" @click="clickArea(value.id)">
                    <input
                            type="checkbox"
                            :disabled="value.disabled"
                            v-model="value.selected"
                            :class="{'checkbox-indeterminate':value.selectedChildrenNum > 0 && value.selectedChildrenNum < value.children.length}">
                    {{value.name}}:
                </div>
                <ul>
                    <li v-for="item in value.children"
                        class="li-province">
                        <div class="pro-parent" @mouseleave.self="proMouseOut($event, item.id)"
                             @mouseenter.self="proMouseOver($event, item.id)">
                            <div class="pro-shadow"></div>
                            <div
                                    class="pro-content"
                                    @click.stop="clickArea(item.id)">
                                <input
                                        type="checkbox"
                                        :disabled="item.disabled"
                                        v-model="item.selected"
                                        :class="{'checkbox-indeterminate':item.selectedChildrenNum > 0 && item.selectedChildrenNum < item.children.length}">
                                <span>{{item.name}}</span>
                            </div>

                            <ul v-if="item.children.length" class="ul-city">
                                <li v-for="citem in item.children" class="li-city">
                                    <div class="city-content" @click.stop="clickArea(citem.id)">
                                        <input type="checkbox" :disabled="citem.disabled" v-model="citem.selected">
                                        <span :title="citem.name">{{citem.name}}</span>
                                    </div>
                                </li>
                            </ul>
                        </div>
                    </li>
                </ul>
            </li>
        </ul>

    </div>
</template>
<script type="text/javascript">
    /**
     * ### 地域组件
     *
     * @methods
     * ** setSelectedItemByNames 根据地域名称，设置选中地域；
     *    如：XX.setSelectedItemByNames('北京;上海;广东')
     *
     * ** setSelectedItemByIds 根据地域ID，设置选中地域；
     *    如：XX.setSelectedItemByIds('id1;id2;id3')
     *
     * ** setDisabled 设置某地域是否可用；
     *    如：XX.setDisabled('id1', true);
     *    - 参数：param1 string 地域id,
                 param2 bool 是否可用
     *
     * ** getSelectedResult 获取当前选中的地域；
     *    返回：Object 包含三个字段：
     *         arr: 选中地域的数组
     *         names: 选中地域的名称字符串，';'分割
     *         ids: 选中地域的ID的字符串，';'分割
     */

    import ui from '../base/ui'
    var originData = require('./regionData');
    // 层级ID
    var regionLevel = {
        country: 10,
        area: 11,
        province: 12,
        city: 13
    };
    var regionData = JSON.parse(JSON.stringify(originData));
    var params = {
        // 地域ID为索引的地域层级树
        regionObj: buildRegionTree(regionData),
        // 地域名为索引的地域层级树
        areaNameTree: buildNameTree(regionData),
        // 根节点的ID
        allRegion: regionData['100001']
    };
    export default {
        params,
        mixins: [ui],
        methods: {
            clear () {
                this.setMineState(this.allRegion.id, false);
            },
            proMouseOver (e, id) {
                var me = this;
                var cTarget = e.target;
                if (me.regionObj[id].children.length) {
                    // setDialogPosition(cTarget, me.$el);
                    me.regionObj[id].mouseover = true;
                    setTimeout(function () {
                        if (me.regionObj[id].mouseover) {
                            let city = cTarget.querySelector('.ul-city');
                            city && (city.style.display = 'block');
                            cTarget.querySelector('.pro-shadow').style.display = 'block';
                        }
                    }, 200);
                }
            },
            proMouseOut (e, id) {
                var cTarget = e.target;
                this.regionObj[id].mouseover = false;
                let city = cTarget.querySelector('.ul-city');
                city && (city.style.display = '');
                cTarget.querySelector('.pro-shadow').style.display = ''
            },
            /* 设置自己以及孩子的选中|disabled状态 */
            setMineState (id, flag, type, isForce) {
                var item = this.regionObj[id];
                var children = item.children;
                var clength = children.length;
                type = type ? type : 'selected'
                if (type == "selected" && item.disabled && !isForce) {
                    return;
                }
                item[type] = flag;
                if (clength) {
                    item.selectedChildrenNum = flag ? clength : 0;
                    for (var i = 0; i < clength; i++) {
                        children[i][type] = flag;
                        this.setMineState(children[i].id, flag, type, isForce);
                    }
                }
            },
            /* 根据自己的选中状态，设置父级的选中状态 */
            setParentState (id, cState) {
                var me = this;
                var pid = me.regionObj[id].parentId;
                if (!+pid) {
                    return
                }
                var citem = me.regionObj[pid];
                citem.selectedChildrenNum += cState ? 1 : -1;
                function doAction(tag) {
                    citem.selected = tag;
                    if (me.regionObj[citem.parentId]) {
                        me.setParentState(citem.id, tag);
                    }
                }

                if (!citem.selected && citem.selectedChildrenNum >= citem.children.length) {
                    doAction(true);
                } else if (citem.selected && citem.selectedChildrenNum < citem.children.length) {
                    doAction(false);
                }
            },
            /* 处理所有地域点击 */
            clickArea (id, forceSet, forceFlag) {
                var cState = forceSet ? forceFlag : !this.regionObj[id].selected;
                this.setMineState(id, cState);
                this.setParentState(id, cState);
            },

            setDisabled (id, flag) {
                id = id ? id : this.allRegion.id;
                flag && (this.clear());
                this.setMineState(id, flag, 'disabled', true);
            },
            /* 根据 地域名 字符串设置地域 */
            setSelectedItemByNames (names) {
                this.clear();
                var areaNameArr = names.split(';');
                for (var i = 0; i < areaNameArr.length; i++) {
                    var citem = this.areaNameTree[areaNameArr[i]];
                    citem && (this.clickArea(citem.id, true, true));
                }
            },
            /* 根据 ID 字符串设置地域 */
            setSelectedItemByIds (ids) {
                this.clear();
                var areaIdArr = ids.split(';');
                for (var i = 0; i < areaIdArr.length; i++) {
                    this.clickArea(areaIdArr[i], true, true);
                }
            },
            getSelectedResult () {
                var selectedArr = [];
                var resNameStr = [];
                var resIdsStr = [];

                function getSelectedItem(item, preName) {
                    if (item.selected && item.level > regionLevel.area) {
                        selectedArr.push(item);
                        resNameStr.push(preName ? preName + '-' + item.name : item.name);
                        resIdsStr.push(item.id);
                    } else if (item.children.length > 0) {
                        var cChildren = item.children;
                        for (var i = 0; i < cChildren.length; i++) {
                            getSelectedItem(cChildren[i],
                                    item.level > regionLevel.area ? item.name : false);
                        }
                    }
                }

                getSelectedItem(this.allRegion);
                return {
                    arr: selectedArr,
                    names: resNameStr.join(';'),
                    ids: resIdsStr.join(';')
                };
            }
        }
    }
    /* 构建一个ID为key的地域层级树 */
    function buildRegionTree(regionObj) {
        var citem = null;
        for (var key in regionObj) {
            if (regionObj.hasOwnProperty(key)) {
                citem = regionObj[key];
                citem.selected = false;
                citem.selectedChildrenNum = 0;
                citem.disabled = false;
                citem.children = citem.children || [];
                if (citem.parentId > 0) {
                    var parentItem = regionObj[citem.parentId];
                    parentItem.children = parentItem.children || [];
                    parentItem.children.push(citem);
                }
            }
        }
        return regionObj;
    }
    /* 构建一个地域名为key的地域层级树 */
    function buildNameTree(regionObj){
        var nameTree = {};
        for (var i in regionObj) {
            var citem = regionObj[i];
            if (regionObj.hasOwnProperty(i) && citem.level > regionLevel.area) {
                if (citem.level < 13) {
                    nameTree[citem.name] = citem;
                }
                for (var j = 0; j < citem.children.length; j++) {
                    var cjitem = citem.children[j];
                    nameTree[citem.name + '-' + cjitem.name] = cjitem;
                    if (!nameTree[cjitem.name]) {
                        nameTree[cjitem.name] = cjitem;
                    }
                }
            }
        }
        return nameTree;
    }
    /* 计算表格边界与弹框的位置，防止弹框逃出边界 */
    function setDialogPosition(content, $parent) {
        var $me = content;
        var $cityDom = $me.querySelector('.ul-city');
        var $shadow = $me.querySelector('.pro-shadow');
        var mo = {left: $me.offsetLeft, top: $me.offsetTop};
        var po = {left: $parent.offsetLeft, top: $parent.offsetTop};
        var preLeft = mo.left - po.left;
        var preTop = mo.top - po.top;
        var lasRight = $parent.clientWidth - preLeft - $me.clientWidth;
        var lasBottom = $parent.clientHeight - preTop - $me.clientHeight;
        var ch = $cityDom.clientHeight;
        var cw = $cityDom.clientWidth;
        var position = {left: -1, top: $me.clientHeight};
        if (lasRight < cw && preLeft > lasRight) {
            position.left = $me.clientWidth - cw + 19;
        }
        if (lasBottom < ch && preTop > lasBottom) {
            position.top = 0 - 2 - ch;
            $shadow.style.top = -2 + 'px';
            console.log(1111);
            $me.addClass('top');
        }
        $cityDom.style.left = position.left + 'px';
        $cityDom.style.top = position.top + 'px';
    }
</script>
<style type="text/css" lang="less">
    .region-component-panel {
        background: #fff;

        input.checkbox-indeterminate {
            background-position: -45px 0;
        }
        .all-region {
            background: #f8f8f8;
            height: 30px;
        }
        ul {
            line-height: 0;
        }
        .area-li {
            margin-top: 5px;
        }
        .area-li, .all-region {
            padding-left: 100px;
            position: relative;
            line-height: 26px;
            .area-content {
                position: absolute;
                left: 10px;
                top: 3px;
            }
            li {
                display: inline-block;
                line-height: 26px;
                position: relative;
                color: #55a055;
                width: 100px;
                font-size: 12px;
                box-sizing: border-box;
            }
        }
        .area-content, .city-content, .pro-content {
            cursor: pointer;
            display: block;
            position: relative;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            input {
                margin-right: 10px;
                cursor: pointer;
                position: relative;
                top: 1px;
                width: 14px;
                height: 14px;
                vertical-align: baseline;
            }
            span {
                width: 26px;
                white-space: pre;
                font-size: 12px;
            }
        }
        .li-province {
            display: inline-block;
            border: 1px solid #fff;
        }
        .ul-city {
            display: none;
            position: absolute;
            background: #fff;
            width: 300px;
            left: -1px;
            top: 100%;
            z-index: 2;
            line-height: 26px;
            border: 1px solid #e4e4e4;
            padding-top: 6px;
        }
        .city-content {
            padding: 0 10px;
        }
        .li-province {
            border: 1px solid #fff;
            .pro-parent {
                border: 1px solid #fff;
                display: block;
                position: relative;
                padding: 0 10px;
                .pro-shadow {
                    width: 100%;
                    height: 9px;
                    background: #fff;
                    top: 90%;
                    position: absolute;
                    z-index: 9;
                    left: 0;
                    display: none;
                }
                &:hover {
                    border: 1px solid #e4e4e4;
                    background: #fff;
                    z-index: 9;
                    box-shadow: 0px 0px 2px #e4e4e4;
                    .ul-city {
                        box-shadow: 0px 0px 2px #e4e4e4;
                    }
                }
            }
        }
    }
</style>


