<template>
<div class="area-picker-demo demo">
    <div class="section">
      <h2 class="demo-title">地域选择</h2> 
      <div class="box demo-action-area">
          <region ref="region" :params="params"></region>
      </div>
      <div class="demo-action-area">
        <div class="selected-area-con">
          <button @click="doGetSelectedInfo">获取已选的区域</button>
          <div class="selected-area-res">
            <p>
                <span v-for="(item, index) in selectedCityName" v-html="item + selectedCityId[index]"></span>
            </p>
          </div>
        </div>
        <div class="selected-area-con">
          <label>
              <input placeholder="输入名称选中，如：北京;上海;广东-广州" type="text" v-model="userInputCity" name="">
              <button @click="doCheckedByName">设置选中区域</button>
          </label>
        </div>
        <div class="selected-area-con">
          <label>
              <input placeholder="输入ID选中，如：16842752;17235968" type="text" v-model="userInputCityId" name="">
              <button @click="doCheckedByIds">设置选中区域</button>
          </label>
        </div>
        <div class="selected-area-con">
          <label>
              <input placeholder="输入ID选中，如：16842752" type="text" v-model="enabledId" name="">
              <button @click="doSetDisabledByIds(true)">设置此项不可选</button>
              <button @click="doSetDisabledByIds(false)">设置此项可选</button>
          </label>
        </div>
      </div>
    </div>
</div>
</template>

<script type="text/javascript">
import Region from 'smui/Region'

export default {
    data() {
        return {
            params: {
                province: 4,
                city: 5
            },
            enabledId: '',
            selectedCityName: [],
            selectedCityId: [],
            userInputCity: '',
            userInputCityId: ''
        }
    },
    components: {
        Region
    },
    methods: {
      doSetDisabledByIds: function(flag){
        this.$refs.region.setDisabled(this.enabledId, flag);
      },
      doCheckedByName: function(argument) {
        this.$refs.region.setSelectedItemByNames(this.userInputCity);
      },
      doCheckedByIds: function(argument) {
        this.$refs.region.setSelectedItemByIds(this.userInputCityId);
      },
      doGetSelectedInfo: function(){
        var res = this.$refs.region.getSelectedResult();
        this.selectedCityId = res.ids.split(';');
        this.selectedCityName = res.names.split(';');
      }
    }
}
</script>
<style type="text/css" lang="less">
  .area-picker-demo{
    .demo-action-area{
      border: 1px solid #efefef;
      margin: 10px;
      .selected-area-con{
        margin: 10px;
        label {
          line-height: 24px;
          vertical-align: middle;
          display: inline-block;
          margin-top: 10px;
          input{
            line-height: 24px;
            padding: 0 10px;
            width: 400px;
            vertical-align: middle;
          }
        } 
      }
      .selected-area-res{
        line-height: 1.5;
        padding: 10px;
        margin: 10px 0;
        word-wrap: break-word;
        border: 1px solid #e4e4e4;
        background-color: #f4f4f4;
        p span{
            border: 1px solid #e4e4e4;
            display: inline-block;
            margin: 2px 5px;
            line-height: 2;
            padding: 0 10px;
            border-radius: 5px;
        }
      }
    }
    
  }
  
</style>


