<template>
<div class="city-picker-demo demo">
    <div class="section">
      <h2 class="demo-title">城市选择</h2> 
      <div class="box">
          <city-picker :params="params"
            @city-change="changeCity"
            @province-change="changeProvince"></city-picker>
      </div> 
      <div class="box" v-if="province">{{province}} / {{city}}</div>
    </div>
</div>
</template>

<script type="text/javascript">
import CityPicker from 'smui/CityPicker'

export default {
    data() {
        return {
            params: {
                province: 4,
                city: 5
            },
            province: '',
            city: ''
        }
    },
    components: {
        CityPicker
    },
    methods: {
        changeCity (e) {
            this.city = e.data.name
        },
        changeProvince (e) {
            let data = e.data
            this.province = data.provinceName
            this.city = data.cityName
        }
    }
}
</script>


